#chords {
  display: flex;
  height: 25vh;
  align-items: flex-end;
}

#searchBar {
  width: 90%;
  font-size: 16px;
  padding: 12px 20px 12px 40px;
  border: 1px solid #ddd;
  margin-bottom: 12px;
}

#backBtn {
  padding: 0em 1em !important;
}

#playlistTable {
  border-collapse: collapse;
  width: 100%;
  border: 1px solid #ddd;
  font-size: 18px;
}
#playlistTable th,
#playlistTable td {
  text-align: left;
  padding: 12px;
}
th {
  border-bottom: #000000 dashed;
}
td {
  border-bottom: #000000 solid;
}
#playlistTable tr {
  border-bottom: 1px solid #ddd;
}
tr:nth-child(even) {
  background-color: #dddddd;
}
#playlistTable tr.header,
#playlistTable tr:hover {
  background-color: #f1f1f1;
}

button {
  margin: 5px;
  /*  */
  box-sizing: initial !important;
  border-width: initial !important;
  border-style: initial !important;
  border-color: initial !important;

  font-family: initial !important;
  font-feature-settings: initial !important;
  font-variation-settings: initial !important;
  font-weight: initial !important;
  line-height: initial !important;
  color: initial !important;
  padding: initial !important;
  /*  */
  font-size: 2rem !important;
}
#paginationNavPrev {
  padding-right: 1em !important;
}
#paginationNavNxt {
  padding-left: 1em !important;
}
#paginationBtns {
  background-color: black;
  color: white !important;
}
#tablePagination {
  display: flex;
  font-size: 2rem;
}
#tablePaginationNavigator {
  text-align: center;
  flex: 70%;
}
#tablePaginationRowsPerPage {
  text-align: end;
  flex: 1;
}
