* {
  user-select: none;
  transition: 0.5s;
}

label {
  width: 150px;
  height: 75px;
  background-color: #d55050;
  box-shadow: inset 0px 6px 0px 0px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  position: absolute;
  top: 7%;
  left: 77%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

input:checked ~ label {
  background-color: #50d5a1;
}

input {
  display: none;
  appearance: none;
}

/* ---------------------- */

* {
  box-sizing: border-box;
  /*   border: 1px solid black; */
}

body {
  font-family: "Lato";
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizelegibility;
}

ul {
  display: flex;
  min-height: 100vh;
  max-height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
  list-style-type: none;
}

li {
  flex: 1;
  display: flex;
  align-items: stretch;
  padding: 20px;
  background: #e74c3c;
  cursor: pointer;
  transition: all 0.5s ease;

  &:hover {
    background: darken(#e74c3c, 3);
  }

  &.active {
    flex: 5;
    background: #fff;
    cursor: default;

    h2 {
      color: #e74c3c;
    }

    .section-content {
      flex: 4;
      opacity: 1;
      transform: scaleX(1);
      color: #e74c3c;
    }
  }

  .section-title {
    flex: 1;
    display: flex;
    align-items: center;
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;

    h2 {
      margin: 0;
      transform: rotate(-90deg);
      white-space: nowrap;
    }
  }

  .section-content {
    flex: 1;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    opacity: 0;
    transition: all 0.25s 0.1s ease-out;
  }
}
